:root {
  --bgcolour: rgba(220, 232, 241, .95);
}

header {
  height: 100px;
  background: linear-gradient(0deg, #3b4890 0%, #1d7cfd 54%, rgba(59, 72, 144, .6) 100%);
  box-shadow: 0 20px 30px rgba(0, 0, 0, .66);
}

p {
  font-family: PT Sans Narrow, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
}

h2 {
  font-family: PT Sans Narrow, sans-serif;
  font-size: 30px;
  font-style: italic;
  font-weight: 700;
}

.ss-main {
  height: 30px;
  background-color: rgba(38, 59, 206, 0);
  background-color: var(--bgcolour);
  border: 3px solid rgba(22, 72, 172, .46);
  border-radius: 15px;
  transition: border-color .5s ease-in-out;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}

.ss-list {
  background-color: #e6f2fb;
}

.ss-content {
  background-color: #e6f2fb;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}

.ss-list::-webkit-scrollbar {
  width: 5px;
  background-color: rgba(51, 51, 230, .435);
  border-radius: 50%;
}

.ss-list::-webkit-scrollbar-thumb {
  border-top: 20px solid #001aff;
  border-radius: 10%;
}

.ss-main:hover {
  border-color: rgba(13, 88, 238, .882);
}

button {
  height: 30px;
  cursor: pointer;
  background-color: rgba(38, 59, 206, 0);
  background-color: var(--bgcolour);
  border: 3px solid rgba(22, 72, 172, .46);
  border-radius: 15px;
  transition: border-color .5s ease-in-out;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}

.button:hover {
  border-color: rgba(13, 88, 238, .882);
}

.regression-button {
  cursor: pointer;
  transition: border-color .5s ease-in-out;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}

.regression-button:hover, .add-fav-button:hover {
  border-color: rgba(13, 88, 238, .882);
}

input {
  height: 22px;
  background-color: rgba(38, 59, 206, 0);
  background-color: var(--bgcolour);
  border: 3px solid rgba(22, 72, 172, .46);
  border-radius: 100px;
  transition: border-color .5s ease-in-out;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}

input:hover, input:focus {
  border-color: rgba(13, 88, 238, .882);
}

body {
  background: rgba(24, 138, 209, .11) url("144025-blue-and-white-square-lines-background-vector-art.b68b5ba5.jpg") 0 0 / cover;
  margin: 0 auto;
}

.main-div {
  max-width: 1000px;
  margin: 40px auto;
}

canvas {
  background-color: (--bgcolour);
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, .5);
}

.flatpickr-calendar.open {
  border-color: rgba(13, 88, 238, .882);
}

.flatpickr-days:not(.flatpickr-day) {
  background-color: rgba(65, 179, 205, .2);
}

.flatpickr-day {
  opacity: 1;
}

.flatpickr-day:nth-child(7n+1), .flatpickr-day:nth-child(7n+7) {
  color: red;
}

.flatpickr-calendar .flatpickr-day.prevMonthDay {
  color: rgba(57, 57, 57, .3);
}

.flatpickr-day.flatpickr-disabled, .flatpickr-day.nextMonthDay {
  color: rgba(57, 57, 57, .1);
}

.tickers-div, .buttons-div {
  justify-content: center;
  gap: 10px;
  padding-bottom: 10px;
  display: flex;
}

.mainNews {
  border-radius: 15px;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  display: flex;
  position: relative;
  box-shadow: 0 0 20px rgba(0, 0, 0, .5);
}

.newsDiv {
  width: 465px;
  height: 70vh;
  background-color: var(--bgcolour);
  border-radius: 30px;
  margin-bottom: 20px;
  padding-top: 10px;
  padding-left: 14px;
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
  box-shadow: 3px -1px 14px rgba(0, 0, 0, .1), 0 1px 3px rgba(0, 0, 0, .08);
}

.newsDiv:nth-child(2n+1):before {
  content: "";
  width: 6px;
  background-color: rgba(51, 51, 230, .435);
  border-radius: 50%;
  margin-left: 15px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -3%;
}

.newsDiv::-webkit-scrollbar {
  width: 5px;
  background-color: rgba(51, 51, 230, .435);
  border-radius: 50%;
}

.newsDiv::-webkit-scrollbar-thumb {
  border-top: 20px solid #001aff;
  border-radius: 10%;
}

.news-paragraph {
  padding-bottom: 10px;
}

.div-afterp {
  min-height: 10px;
}

.short-data {
  max-width: 1200px;
  justify-content: space-evenly;
  margin: 0 auto;
  padding-top: 5px;
  display: flex;
}

.short-data-div {
  flex-flow: row;
  align-items: center;
  display: flex;
}

.delete-button {
  box-shadow: none;
  color: #c30000;
  background-color: inherit;
  border-style: none;
  font-size: 25px;
}

.single-data {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.header-paragraph {
  color: #e6e6f0;
  margin: 0;
  padding: 2px;
  font-family: PT Sans Narrow, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
}

.value-plus {
  color: #87c487;
}

.value-minus {
  color: #ec7c7c;
}

.chart-div {
  background-color: var(--bgcolour);
  border-radius: 20px;
}

.data-and-chart {
  max-width: 1000px;
  border-radius: 15px;
  justify-content: center;
  gap: 35px;
  margin-top: 10px;
  padding: 10px;
  display: flex;
  box-shadow: 0 0 20px rgba(0, 0, 0, .5);
}

#currentData {
  min-width: 200px;
  width: 300px;
  background-color: var(--bgcolour);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, .5);
}

.pChange {
  justify-content: center;
  display: flex;
}

.historical-data {
  flex-direction: column;
  display: flex;
}

.ticker-data {
  justify-content: space-between;
  display: flex;
}

historical-paragraph-date {
  margin-bottom: 5px;
  font-size: 18px;
}

.historical-paragraph {
  margin: 2px;
  font-size: 15px;
}

@media screen and (max-width: 420px) {
  .main-div {
    max-width: 350px;
    margin: auto 20px;
  }

  .tickers-div {
    max-width: 350px;
    flex-direction: column;
    align-items: center;
    padding-bottom: 15px;
  }

  .select1 {
    width: 320px;
    background-color: var(--bgcolour);
    margin-bottom: 15px;
    font-size: 20px;
  }

  .select2 {
    width: 320px;
    background-color: var(--bgcolour);
    font-size: 20px;
  }

  .buttons-div {
    max-width: 350px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    display: flex;
  }

  canvas {
    height: 200px;
    width: 320px;
  }
}

